"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { Search } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent } from "@/components/ui/card"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { ordersAPI, getCurrentUser, authAPI, Order } from "@/lib/api"
import { useToast } from "@/hooks/use-toast"

export default function AdminOrders() {
  const router = useRouter()
  const { toast } = useToast()
  const [orders, setOrders] = useState<Order[]>([])
  const [searchQuery, setSearchQuery] = useState("")
  const [statusFilter, setStatusFilter] = useState("All")
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Check authentication
    const user = getCurrentUser()
    if (!user || !user.is_admin) {
      router.push("/login")
      return
    }

    fetchOrders()
  }, [router])

  const fetchOrders = async () => {
    try {
      const data = await ordersAPI.getAll()
      setOrders(data)
    } catch (error: any) {
      if (error.message.includes("401") || error.message.includes("403")) {
        authAPI.logout()
        router.push("/login")
      } else {
        toast({
          title: "Error",
          description: "Failed to load orders",
          variant: "destructive",
        })
      }
    } finally {
      setIsLoading(false)
    }
  }

  const handleStatusUpdate = async (orderId: number, newStatus: string) => {
    try {
      await ordersAPI.update(orderId, { status: newStatus })
      toast({
        title: "Success",
        description: "Order status updated successfully",
      })
      fetchOrders()
    } catch (error: any) {
      toast({
        title: "Error",
        description: error.message || "Failed to update order status",
        variant: "destructive",
      })
    }
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleDateString("en-US", { year: "numeric", month: "short", day: "numeric" })
  }

  const filteredOrders = orders.filter((order) => {
    const matchesSearch =
      order.id.toString().includes(searchQuery) ||
      order.customer_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      order.customer_email.toLowerCase().includes(searchQuery.toLowerCase())
    const matchesStatus = statusFilter === "All" || order.status === statusFilter
    return matchesSearch && matchesStatus
  })

  if (isLoading) {
    return (
      <div className="p-8 flex items-center justify-center min-h-screen">
        <p className="text-muted-foreground">Loading...</p>
      </div>
    )
  }

  return (
    <div className="p-8">
      <div className="mb-8">
        <h1 className="text-3xl font-bold">Orders</h1>
        <p className="text-muted-foreground" style={{ fontFamily: "var(--font-inter)" }}>
          Manage and track customer orders
        </p>
      </div>

      {/* Filters */}
      <div className="flex flex-col md:flex-row gap-4 mb-6">
        <div className="relative flex-1 max-w-md">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
          <Input
            placeholder="Search orders..."
            className="pl-10"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
        </div>
        <div className="flex gap-2">
          {["All", "Pending", "Processing", "Shipped", "Delivered"].map((status) => (
            <Button
              key={status}
              variant={statusFilter === status ? "default" : "outline"}
              size="sm"
              onClick={() => setStatusFilter(status)}
            >
              {status}
            </Button>
          ))}
        </div>
      </div>

      {/* Orders Table */}
      <Card>
        <CardContent className="p-0">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="border-b border-border bg-muted/50">
                  <th
                    className="text-left py-4 px-6 text-sm font-medium text-muted-foreground"
                    style={{ fontFamily: "var(--font-inter)" }}
                  >
                    Order ID
                  </th>
                  <th
                    className="text-left py-4 px-6 text-sm font-medium text-muted-foreground"
                    style={{ fontFamily: "var(--font-inter)" }}
                  >
                    Customer
                  </th>
                  <th
                    className="text-left py-4 px-6 text-sm font-medium text-muted-foreground"
                    style={{ fontFamily: "var(--font-inter)" }}
                  >
                    Date
                  </th>
                  <th
                    className="text-left py-4 px-6 text-sm font-medium text-muted-foreground"
                    style={{ fontFamily: "var(--font-inter)" }}
                  >
                    Items
                  </th>
                  <th
                    className="text-left py-4 px-6 text-sm font-medium text-muted-foreground"
                    style={{ fontFamily: "var(--font-inter)" }}
                  >
                    Total
                  </th>
                  <th
                    className="text-left py-4 px-6 text-sm font-medium text-muted-foreground"
                    style={{ fontFamily: "var(--font-inter)" }}
                  >
                    Status
                  </th>
                  <th
                    className="text-left py-4 px-6 text-sm font-medium text-muted-foreground"
                    style={{ fontFamily: "var(--font-inter)" }}
                  >
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody>
                {filteredOrders.length === 0 ? (
                  <tr>
                    <td colSpan={7} className="py-8 text-center text-muted-foreground">
                      No orders found
                    </td>
                  </tr>
                ) : (
                  filteredOrders.map((order) => (
                    <tr key={order.id} className="border-b border-border last:border-0">
                      <td className="py-4 px-6 font-medium" style={{ fontFamily: "var(--font-inter)" }}>
                        #{order.id}
                      </td>
                      <td className="py-4 px-6">
                        <div>
                          <p className="font-medium">{order.customer_name}</p>
                          <p className="text-sm text-muted-foreground">{order.customer_email}</p>
                        </div>
                      </td>
                      <td className="py-4 px-6" style={{ fontFamily: "var(--font-inter)" }}>
                        {formatDate(order.created_at)}
                      </td>
                      <td className="py-4 px-6" style={{ fontFamily: "var(--font-inter)" }}>
                        {Array.isArray(order.items) ? order.items.length : 0}
                      </td>
                      <td className="py-4 px-6" style={{ fontFamily: "var(--font-inter)" }}>
                        ${order.total.toFixed(2)}
                      </td>
                      <td className="py-4 px-6">
                        <span
                          className={`px-2 py-1 text-xs rounded-full ${
                            order.status === "Delivered"
                              ? "bg-green-100 text-green-800"
                              : order.status === "Shipped"
                                ? "bg-blue-100 text-blue-800"
                                : order.status === "Processing"
                                  ? "bg-purple-100 text-purple-800"
                                  : "bg-yellow-100 text-yellow-800"
                          }`}
                          style={{ fontFamily: "var(--font-inter)" }}
                        >
                          {order.status}
                        </span>
                      </td>
                      <td className="py-4 px-6">
                        <Select
                          value={order.status}
                          onValueChange={(value) => handleStatusUpdate(order.id, value)}
                        >
                          <SelectTrigger className="w-32">
                            <SelectValue />
                          </SelectTrigger>
                          <SelectContent>
                            <SelectItem value="Pending">Pending</SelectItem>
                            <SelectItem value="Processing">Processing</SelectItem>
                            <SelectItem value="Shipped">Shipped</SelectItem>
                            <SelectItem value="Delivered">Delivered</SelectItem>
                          </SelectContent>
                        </Select>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
